ues/12681\n  // same method as Select\n  &:empty {\n    display: inline-block;\n    width: 0;\n    visibility: hidden;\n    content: '\\a0';\n  }\n}\n\na.@{btn-prefix-cls} {\n  // Fixing https://github.com/ant-design/ant-design/issues/12978\n  // It is a render problem of chrome, which is only happened in the codesandbox demo\n  // 0.1px for padding-top solution works and I don't why\n  padding-top: 0.1px;\n  line-height: @btn-height-base - 2px;\n\n  &-lg {\n    line-height: @btn-height-lg - 2px;\n  }\n  &-sm {\n    line-height: @btn-height-sm - 2px;\n  }\n}\n\n@import './rtl';\n","// mixins for button\n// ------------------------\n.button-size(@height; @padding-horizontal; @font-size; @border-radius) {\n  @padding-vertical: max(\n    round((@height - @font-size * @line-height-base) / 2 * 10) / 10 - @border-width-base,\n    0\n  );\n  height: @height;\n  padding: @padding-vertical @padding-horizontal;\n  font-size: @font-size;\n  border-radius: @border-radius;\n}\n\n.button-disabled(@color: @btn-disable-color; @background: @btn-disable-bg; @border: @btn-disable-border) {\n  &-disabled,\n  &.disabled,\n  &[disabled] {\n    &,\n    &:hover,\n    &:focus,\n    &:active,\n    &.active {\n      .button-color(@color; @background; @border);\n\n      text-shadow: none;\n      box-shadow: none;\n    }\n  }\n}\n\n.button-variant-primary(@color; @background) {\n  .button-color(@color; @background; @background);\n\n  text-shadow: @btn-text-shadow;\n  box-shadow: @btn-primary-shadow;\n\n  &:hover,\n  &:focus {\n    & when (@theme = dark) {\n      .button-color(\n        @color; ~`colorPalette('@{background}', 7) `; ~`colorPalette('@{background}', 7) `\n      );\n    }\n    & when not (@theme = dark) {\n      .button-color(\n        @color; ~`colorPalette('@{background}', 5) `; ~`colorPalette('@{background}', 5) `\n      );\n    }\n  }\n\n  &:active,\n  &.active {\n    & when (@theme = dark) {\n      .button-color(\n        @color; ~`colorPalette('@{background}', 5) `; ~`colorPal